import React from "react"
import Typography from "@material-ui/core/Typography"
import { Grid } from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"


const useStyles = makeStyles((theme) => ({
    welcomeContainer: {
        width: '100%',
        height: "max-content",
        background: 'white',
        padding: '50px',
        boxSizing: 'border-box',
        color: '#00287b',
        [theme.breakpoints.down("sm")]: {
            padding: '0px',
        },
    },
    welcomeTextContainer: {
        justifyContent: 'center',
        alignItems: 'center',
        padding: theme.spacing(4),
        [theme.breakpoints.up("lg")]: {
            padding: theme.spacing(10),
        },
    },
    welcomeVideo: {
        width: '100%'
    },
    welcomeSubheading: {
        marginTop: '6%',
        [theme.breakpoints.up("xl")]: {
            fontSize: '30px',
        },
        [theme.breakpoints.down("sm")]: {
            textAlign:'center'
        },
    },
    welcomeHeading: {
        [theme.breakpoints.up("xl")]: {
            fontSize: '70px',
        },
        [theme.breakpoints.down("sm")]: {
            textAlign:'center'
        },
    }
}))

const Welcome = () => {
    const classes = useStyles()
    return (
        <Grid
            container
            spacing={12}
            justify="center"
            className={classes.welcomeContainer}
        >
            <Grid className={classes.welcomeTextContainer} item xs={12} sm={12} md={6} lg={6} xl={6}>
                <Typography className={classes.welcomeHeading} variant="h4" align='left'>
                    Welcome to the Sapiens University Portal!
                </Typography>
                <Typography className={classes.welcomeSubheading} variant="h6" align='left'>
                    Step inside to discover learning adventures, milestones to cross, the tools you need and a supporting team.
                </Typography>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                <video className={classes.welcomeVideo} controls src='http://techslides.com/demos/sample-videos/small.webm' type='video/webm'>
                </video>
            </Grid>

        </Grid>
    )
}

export default Welcome
